import React, { useEffect,useState } from 'react';

function Feed() {
var Feedi=["lbYz2AsMypM","lbYz2AsMypM","lbYz2AsMypM"]
  const [Feeder, setFeeder] = useState(Feedi);
  const callAboutpage = async () =>{
    try {
      const res = await fetch('/fid',{
        method:"GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        }
      });

      const data = await res.json();
      Feedi=data;
      setFeeder(Feedi);
      if(res.status!== 200){
        const error = new Error(res.error);
        throw error;
      }
    } catch (err) {
      console.log(err)
    }
  }
  useEffect(() => {
    callAboutpage();
  }, []);







  
  const Feedcard = Feeder.map((currentv,ind)=>{
    return(
       <div className="middle-container d-flex justify-content-between align-items-center mt-3 p-2 shadow-dreamy" key={ind} style={{margin:"3%",background:"white"}}>
            <iframe src={"https://www.youtube.com/embed/"+currentv} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" style={{minHeight:"183px",
    height:"71%",
    width:"100%",
    borderRadius:"6px"}}></iframe>
            </div>
    )
  });
  return (
    <>
    <div style={{marginTop:"1%",position:"absolute",width:"100%"}}>
      <div className="middle-container d-flex justify-content-between align-items-center mt-3 p-2 shadow-dreamy" style={{margin:"3%",background:"white"}}><h2 style={{margin:"0",
    fontSsize:"20px",
    lineHeight:"1em",
    paddingRight:"16px",color:"#0a58ca"}}><center>What people thinks about us</center></h2></div>
      {Feedcard}
      <span style={{height:"80px",width:"100%",position:"absolute"}}></span>
</div>
    </>
  );
}

export default Feed;
